import Vuex from 'vuex';
import Vue from 'vue'

import placesDetails from './data/placesDetails';

Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
      count: 100,
      places: placesDetails,
      dist: '',
      lat: '',
      nameSource: ''
    },
    mutations: {
      increment (state) {
        state.count++
      },
      setDistrict (state, dist){
          state.dist = dist;
      },
      setNameSource (state, nameSource){
        state.nameSource = nameSource;
      }
    }, 
    getters: {
      getPlaceById: (state) => (id) => {
        return state.places.find(place => place.id === id)
      }
    }
});

export default store;